	module.exports = {"data":{"markdownRemark":{"html":"<p>HTML has a special element for creating unordered lists, or bullet point-style lists.</p>\n<p>Unordered lists start with a <code>&#x3C;ul></code> element. Then they contain some number of <code>&#x3C;li></code> elements.</p>\n<p>For example:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code>    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>ul</span><span class=\"token punctuation\">></span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>li</span><span class=\"token punctuation\">></span></span>milk<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>li</span><span class=\"token punctuation\">></span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>li</span><span class=\"token punctuation\">></span></span>cheese<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>li</span><span class=\"token punctuation\">></span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>ul</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<p>would create a bullet point-style list of “milk” and “cheese”.</p>","fields":{"slug":"/certificates/create-a-bulleted-unordered-list/"},"frontmatter":{"title":"Create a Bulleted Unordered List"}}},"pathContext":{"slug":"/certificates/create-a-bulleted-unordered-list/"}}

